
@media only screen and (max-width: 768px) {
body {
    width: max(100%, 11em);
}
}
@media only screen and (min-width: 769px) {
body {
    width: max(70%, 11em);
    margin: 1em auto;
}
}
h1 {
  font-family: sans-serif;
  font-size: 18pt;
}
h2 {
  font-family: sans-serif;
  font-size: 10pt;
  color: #333;
}
code {
  font-family: monospace;
  color: rgb(156, 0, 70);
}
